import React from 'react';
import styles from './NavBar.module.css';
import CartIcon from '../Cart/CartIcon/CartIcon';
import LogInLodOut from '../LogInLogOut/LogInLogOut';
import { useDispatch } from 'react-redux';
import { openSideBar } from '../../store/reducers/comicsCartReducer';

function NavBar() {
	const dispatch = useDispatch();

	const handleOpenSideBar = () => {
		dispatch(openSideBar());
	};

	return (
		<div className={styles.nav}>
			<div className={styles.home}>
				<svg xmlns='http://www.w3.org/2000/svg' fill='#fff' width={20} height={20} viewBox='0 0 576 512'>
					<path d='M575.8 255.5c0 18-15 32.1-32 32.1l-32 0 .7 160.2c0 2.7-.2 5.4-.5 8.1l0 16.2c0 22.1-17.9 40-40 40l-16 0c-1.1 0-2.2 0-3.3-.1c-1.4 .1-2.8 .1-4.2 .1L416 512l-24 0c-22.1 0-40-17.9-40-40l0-24 0-64c0-17.7-14.3-32-32-32l-64 0c-17.7 0-32 14.3-32 32l0 64 0 24c0 22.1-17.9 40-40 40l-24 0-31.9 0c-1.5 0-3-.1-4.5-.2c-1.2 .1-2.4 .2-3.6 .2l-16 0c-22.1 0-40-17.9-40-40l0-112c0-.9 0-1.9 .1-2.8l0-69.7-32 0c-18 0-32-14-32-32.1c0-9 3-17 10-24L266.4 8c7-7 15-8 22-8s15 2 21 7L564.8 231.5c8 7 12 15 11 24z' />
				</svg>
			</div>
			<div className={styles.personalAccount}>
				<LogInLodOut />
			</div>
			<div onClick={handleOpenSideBar} className={styles.cart}>
				<svg xmlns='http://www.w3.org/2000/svg' width={20} height={20} viewBox='0 0 20 20' fill='#fff' class='size-5'>
					<path d='M1 1.75A.75.75 0 0 1 1.75 1h1.628a1.75 1.75 0 0 1 1.734 1.51L5.18 3a65.25 65.25 0 0 1 13.36 1.412.75.75 0 0 1 .58.875 48.645 48.645 0 0 1-1.618 6.2.75.75 0 0 1-.712.513H6a2.503 2.503 0 0 0-2.292 1.5H17.25a.75.75 0 0 1 0 1.5H2.76a.75.75 0 0 1-.748-.807 4.002 4.002 0 0 1 2.716-3.486L3.626 2.716a.25.25 0 0 0-.248-.216H1.75A.75.75 0 0 1 1 1.75ZM6 17.5a1.5 1.5 0 1 1-3 0 1.5 1.5 0 0 1 3 0ZM15.5 19a1.5 1.5 0 1 0 0-3 1.5 1.5 0 0 0 0 3Z' />
				</svg>
				<CartIcon />
			</div>
		</div>
	);
}

export default NavBar;
